import React, { useEffect, useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { Button, Modal } from 'react-bootstrap';
import Loader from '../components/Loader';

function ExerciseEndModal({
  isModalShow,
  setModalShow,
  exerciseType,
  exerciseGoal,
  exerciseDirection,
  exerciseMass,
  startTime,
  count,
}) {
  const userId = useRef('');
  const endtime = useRef('');
  useEffect(() => {
    userId.current = localStorage.getItem('auth');
  }, []);
  const [isLoading, setIsLoading] = useState(false);

  const navigate = useNavigate();
  //     
  const sendExerciseRecord = () => {
    endtime.current = new Date();
    setIsLoading(true);
    const exerciseRecord = {
      id: userId.current,
      //date startTime yyyy-mm-dd 
      date: startTime.current.toISOString().slice(0, 10),
      // start_time startTime hh-mm-ss 
      start_time: startTime.current.toISOString().slice(11, 19),
      // end_time endtime hh-mm-ss 
      end_time: endtime.current.toISOString().slice(11, 19),
      // exercise_time endTime - startTime hh-mm-ss 
      exercise_time: new Date(endtime.current - startTime.current)
        .toISOString()
        .slice(11, 19),
      exercise_name: exerciseType,
      // mass 0  null,   int 
      mass: exerciseMass === 0 ? null : parseInt(exerciseMass),
      count: parseInt(count),
      aim_set: 1, //test
      exercised_set: 1, //test
    };

    //   
    axios
      .post('https://shrack.p-e.kr/exercise_end', exerciseRecord)
      .then(res => {
        console.log(res);
        //   
        window.location.href = process.env.PUBLIC_URL;
      })
      .catch(err => {
        console.log(err);
      })
      .finally(() => {
        setIsLoading(false);
      });
  };
  return (
    <div>
      <Modal
        show={isModalShow}
        onHide={() => setModalShow(false)}
        backdrop="static"
        keyboard={false}
        centered>
        <Modal.Header closeButton>
          <Modal.Title> . </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p> : {exerciseType}</p>
          <p>  : {count}</p>
          <p> : {exerciseMass}</p>
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="secondary"
            onClick={() => {
              setModalShow(false);
            }}>
            
          </Button>
          <Button
            variant="primary"
            onClick={() => {
              console.log(' ');
              setModalShow(false);
              sendExerciseRecord();
            }}>
            
          </Button>
        </Modal.Footer>
      </Modal>
      {isLoading && <Loader />}
    </div>
  );
}

export default ExerciseEndModal;
