import React from 'react';
import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import {
  Button,
  Form,
  Container,
  FloatingLabel,
  Alert,
  Row,
  Col,
  Navbar,
} from 'react-bootstrap';
import axios from 'axios';
import Loader from '../components/Loader';

function Signin() {
  const [id, setId] = useState('');
  const [password, setPassword] = useState('');
  const [isAlert, setIsAlert] = useState(false);

  const [isLoading, setIsLoading] = useState(false);
  useEffect(() => {
    console.log(id, password);
  }, [id, password]);

  // onSubmit -> Id Password   
  const onSubmit = e => {
    //id   password  return
    if (!id || !password) {
      e.preventDefault();
      setIsAlert(true);

      return;
    }

    const data = {
      id: id,
      password: password,
    };
    const headers = {
      'Content-Type': 'application/json',
    };
    e.preventDefault();

    //console.log(data);
    //  
    setIsLoading(true);
    axios
      .post('https://shrack.p-e.kr/login', JSON.stringify(data), {
        headers,
      })
      .then(res => {
        e.preventDefault();
        console.log(res);
        localStorage.setItem('auth', res.data.id);
        console.log(localStorage.getItem('auth'));
        //   
        window.location.href = process.env.PUBLIC_URL;
      })
      .catch(err => {
        console.log(err);
        setIsAlert(true);
      })
      .finally(() => {
        setIsLoading(false);
      });
  };
  return (
    <Container>
      <Navbar sticky="top" bg="light" expand="lg">
        <Navbar.Brand href="#home">SHRACK</Navbar.Brand>
        <Navbar.Text className="mr-auto"></Navbar.Text>
      </Navbar>
      <Form onSubmit={onSubmit}>
        <Form.Group className="mb-3" controlId="id">
          <FloatingLabel controlId="id" label="" className="mb-3">
            <Form.Control
              type="ID"
              placeholder=""
              onChange={e => setId(e.target.value)}
            />
          </FloatingLabel>
        </Form.Group>
        <Form.Group className="mb-3" controlId="password">
          <FloatingLabel controlId="password" label="" className="mb-3">
            <Form.Control
              type="password"
              placeholder=""
              onChange={e => setPassword(e.target.value)}
            />
          </FloatingLabel>
        </Form.Group>
        {isAlert && (
          <Alert variant="warning" onClose={() => setIsAlert(false)}>
              .
          </Alert>
        )}
        <Row>
          <Col className="d-flex justify-content-center align-items-center">
            <Button size="lg" variant="primary" type="signIn">
              
            </Button>
          </Col>
          <Col className="d-flex justify-content-center align-items-center">
            <Link to="/signup">
              <Button size="lg" variant="primary" type="signUp">
                
              </Button>
            </Link>
          </Col>
        </Row>
      </Form>
      {isLoading && <Loader />}
    </Container>
  );
}

export default Signin;
