import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

import { Button, Form, Alert, Row, Col, Modal } from 'react-bootstrap';

function ExerciseStartModal({ isModalShow, setModalShow }) {
  const navigate = useNavigate();
  const [exerciseType, setExerciseType] = useState('');
  const [exerciseGoal, setExerciseGoal] = useState(0);
  const [exerciseMass, setExerciseMass] = useState(0);
  const [exerciseDirection, setExerciseDirection] = useState('');

  const [isAlert, setIsAlert] = useState(false);

  const handleModalSubmit = () => {
    // exerciseType exerciseGoal exerciseDirection   Alert 
    if (exerciseType === '' || exerciseGoal === 0 || exerciseDirection === '') {
      setIsAlert(true);
      return;
    }
    // '/record' ,   exerciseType, exerciseGoal, exerciseDirection, exerciseMass  
    navigate('/record', {
      state: {
        exerciseType: exerciseType,
        exerciseGoal: exerciseGoal,
        exerciseDirection: exerciseDirection,
        exerciseMass: exerciseMass,
      },
    });

    setModalShow(false);
  };

  const yourExercisetypes = () => {
    //  
    const exampleExerciseTypes = ['pushup', 'squat'];
    return exampleExerciseTypes.map((type, index) => {
      let message = '';
      if (type === 'pushup') {
        message = '';
      } else if (type === 'squat') {
        message = '';
      }

      return (
        <option key={index} value={type}>
          {message}
        </option>
      );
    });
  };

  return (
    <div>
      <Modal
        show={isModalShow}
        onHide={() => setModalShow(false)}
        backdrop="static"
        keyboard={false}
        centered>
        <Modal.Header closeButton>
          <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Row>
              <Col>
                <Form.Group controlId="exerciseType">
                  <Form.Label>  </Form.Label>
                  <Form.Select
                    value={exerciseType}
                    onChange={e => setExerciseType(e.target.value)}>
                    <option value=""> </option>
                    {yourExercisetypes()}
                  </Form.Select>
                </Form.Group>
              </Col>
              <Col>
                {/*   left right  */}
                <Form.Group controlId="exerciseDirection">
                  <Form.Label>  </Form.Label>
                  <Form.Select
                    value={exerciseDirection}
                    onChange={e => setExerciseDirection(e.target.value)}>
                    <option value=""> </option>
                    <option value="left"></option>
                    <option value="right"></option>
                  </Form.Select>
                </Form.Group>
              </Col>
            </Row>
            <br />
            <Row>
              <Col>
                <Form.Group controlId="exerciseGoal">
                  <Form.Label> ()</Form.Label>
                  <Form.Control
                    as="input"
                    type="number"
                    value={exerciseGoal}
                    min={0}
                    onChange={e => {
                      setExerciseGoal(parseInt(e.target.value));
                    }}></Form.Control>
                </Form.Group>
              </Col>
              <Col>
                <Form.Group controlId="exerciseMass">
                  <Form.Label> (kg)</Form.Label>
                  <Form.Control
                    as="input"
                    type="number"
                    value={exerciseMass}
                    min={0}
                    onChange={e => {
                      setExerciseMass(parseInt(e.target.value));
                    }}></Form.Control>
                </Form.Group>
              </Col>
            </Row>
            <br />
            <Row>
              <Alert variant="secondary">
                <ul>
                  <li style={{ textAlign: 'left' }}>
                       .{' '}
                  </li>
                  <li style={{ textAlign: 'left' }}>
                        .
                  </li>
                </ul>
              </Alert>
            </Row>
            <br />
            <Row>
              {isAlert && (
                <Alert variant="warning">
                     ,  
                </Alert>
              )}
            </Row>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <br />
          <Button variant="primary" onClick={handleModalSubmit}>
            OK
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default ExerciseStartModal;
