import React from 'react';
import { useEffect, useState, useRef } from 'react';
import {
  Button,
  Container,
  Row,
  Col,
  Modal,
  Navbar,
  ProgressBar,
} from 'react-bootstrap';
import style from './Record.module.css';
import CameraComponent from './Camera';
import ExerciseEndModal from './ExerciseEndModal';

import { useLocation } from 'react-router-dom';

function Record() {
  // ExerciseStartModal  
  const location = useLocation();
  const { exerciseType, exerciseGoal, exerciseDirection, exerciseMass } =
    location.state;

  // count  
  const [count, setCount] = useState(0);

  const [time, setTime] = useState(0);

  // camera     
  const [hasPermission, setHasPermission] = useState(null);
  const startTime = useRef(null);
  useEffect(() => {
    startTime.current = new Date();
    console.log(startTime.current);
  }, []);

  // hasPermission true  1 time 1 
  useEffect(() => {
    if (hasPermission) {
      // startTime   

      const interval = setInterval(() => {
        setTime(prevTime => prevTime + 1);
      }, 1000);
      return () => clearInterval(interval);
    }
  }, [hasPermission]);

  //    state
  //   ,    true 
  const [isExerciseEnd, setIsExerciseEnd] = useState(false);
  // count exerciseGoal  isExerciseEnd true , setShowModal true 
  useEffect(() => {
    if (count === exerciseGoal) {
      setIsExerciseEnd(true);
      setShowModal(true);
    }
  }, [count, exerciseGoal]);

  //   Modal
  const [showModal, setShowModal] = useState(false);
  const handleModalOpen = () => {
    setShowModal(true);
  };

  return (
    <div>
      <Navbar bg="primary" variant="dark" expand="xs" fluid>
        <Container>
          <Navbar.Brand>SHrack -   </Navbar.Brand>
        </Container>
      </Navbar>
      <Container>
        <Row className="justify-content-center">
          <div className={style.item_camera}>
            <CameraComponent
              count={count}
              setCount={setCount}
              hasPermission={hasPermission}
              setHasPermission={setHasPermission}
              exerciseType={exerciseType}
              exerciseGoal={exerciseGoal}
              exerciseDirection={exerciseDirection}
              exerciseMass={exerciseMass}
              isExerciseEnd={isExerciseEnd}
            />
          </div>
        </Row>

        <div className={style.item} style={{ padding: '0.5vh' }}>
          <div>
            <ProgressBar
              animated
              now={(count / exerciseGoal) * 100}></ProgressBar>
            <h2>
              {count} / {exerciseGoal}
            </h2>
          </div>
          <div>
            <div>
              {parseInt(time / 60)
                .toString()
                .padStart(2, '0')}{' '}
              : {(time % 60).toString().padStart(2, '0')}
            </div>
          </div>
          <div className="d-grid gap-2">
            <Button onClick={handleModalOpen} style={{ fontSize: '1.25rem' }}>
               
            </Button>
          </div>
        </div>
      </Container>
      <ExerciseEndModal
        isModalShow={showModal}
        setModalShow={setShowModal}
        count={count}
        exerciseType={exerciseType}
        exerciseGoal={exerciseGoal}
        exerciseMass={exerciseMass}
        startTime={startTime}
      />
    </div>
  );
}

export default Record;
